import React from 'react'

import { LayoutLottery, TitleBox } from "components";
import { DepositAndWithdraw } from 'moduls'
import "./style.scss";



const Deposit = () => {

    return (
        <LayoutLottery>
            <div id="page-deposit" className=" container-cst mt-5 pt-5">
                <div className="deposit-cont">
                    <TitleBox name="예치금 충전" />
                    <DepositAndWithdraw type="DEPOSIT" />
                </div>

            </div>
        </LayoutLottery>
    )
}

export default Deposit;
